<template>
  <div>
    <!-- vue-js-modalの表示および設定 -->
    <modal name="first-modal"
    class="first-modal"
    :draggable="false"
    :resizable="false"
    :adaptive="true"
    transition="modalTransition"
    overlayTransition="modalTransition"
    width="50%"
    height="50%"
    styles="
      background-color: rgba(0,0,0,0);
      border-radius: 0px;
      overflow: visible;
      box-shadow: 0 0px 0px 0 rgba(0,0,0,0)
      "
    >
      <!-- モーダルの内容物 -->
      <div class="modal-body">
        <!-- Loading.vueコンポーネントの読み込み
        モーダル内に配置しないとLoading.vue内のトランジションが機能しない-->
        <Loading v-if="isLoading"></Loading>
        <!-- 動画部分 -->
        <video src="@/assets/background.mp4"
        controls
        preload="auto"></video>
        <!-- ウィンドウを閉じるボタン -->
        <button class="modal-close" @click="hide">
          <BatsuIcon></BatsuIcon>
        </button>
      </div>  
    </modal>
  </div>
</template>

<script>
import Loading from '../atoms/Loading.vue'
import BatsuIcon from '../atoms/BatsuIcon.vue'

export default {
  // モジュール名をエクスポート
  name: 'FirstViewModal',
  data:()=>({
    isLoading: true,
  }),
  // 初回ロード時にモーダルを表示する
  mounted(){
    this.$modal.show('first-modal');
    // ロード完了時(今回は指定秒数経過)でLoading.vueを消す
    setTimeout(() => {
      this.isLoading = false
    }, 100)
  },
  // モーダルを閉じるメソッド
  methods: {
    hide : function () {
      this.$modal.hide('first-modal');
    },
  },
  components:{
    Loading,
    BatsuIcon
  }
}
</script>

<style scoped>
.modalTransition-enter,
.modalTransition-leave-to{
  opacity: 0;
}

.modalTransition-leave-active{
  transition: opacity 1s;
}


.first-modal{
  background-color: rgba(0,0,0,.7);
  width: 100vw;
  height: 100vh;
  z-index: 1001;
}

.modal-body{
      z-index: 1000;
      display: flex;
      flex-flow: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: auto;
}

video{
  width: 100%;
  height: auto;
}

.modal-close{
  position: absolute;
  width: 2em;
  height: 3em;
  top: -10%;
  right: -45px;
  border: transparent;
  background: transparent;
  padding: 0;
}
</style>
